.app-header {
    max-height: var(--app-header-height);
    //background-color: var(--color-primary);
    color:      var(--color-primary-font);
    z-index:    var(--zindex-header);
    //@include bar(var(--app-header-height), 0, var(--app-layout-padding));

    &, > .app-container {
        height: var(--app-header-height);
    }

    > .app-container {
        height: var(--app-header-height);
    }

    .logo {
        height:         3.4em;
        //margin-right: 2rem;
        pointer-events: none;
    }

    .title {
        font-size:   4rem;
        font-weight: 500;
        margin:      0;
    }
}
